.darkMode{
    background-color: darken($grisOscuro, 10);

    h1,
    h2,
    h3,
    h4,
    p{
        color: $gris;
    }


    .contenedor-info{
        border: 2rem;
        background-color: $grisOscuro;
        border: 1px solid $gris;
        color: $negro;
    }

    .formulario{

        legend,
        label{
            color: $gris;
        }
        
        input:not([type="submit"]),
        textarea,
        select{
            background-color: $grisOscuro;
            color: $gris;
            border: solid $grisOscuro;
        }
    }
}
