/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
  color: #000000;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-amarillo {
  background-color: #E08709;
  text-decoration: none;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem;
  display: inline-block;
  width: 20rem;
}
.boton-amarillo:hover {
  background-color: #c77808;
}

.boton-amarillo-block {
  background-color: #E08709;
  text-decoration: none;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem;
  display: block;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
}

.boton-verde {
  border: none;
  cursor: pointer;
  background-color: #71B100;
  text-decoration: none;
  font-weight: 700;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem;
  display: inline-block;
  margin: 2rem 4rem;
}
.boton-verde:hover {
  background-color: #619800;
}

.darkMode {
  background-color: #1a1a1a;
}
.darkMode h1,
.darkMode h2,
.darkMode h3,
.darkMode h4,
.darkMode p {
  color: #e1e1e1;
}
.darkMode .contenedor-info {
  border: 2rem;
  background-color: #333333;
  border: 1px solid #e1e1e1;
  color: #000000;
}
.darkMode .formulario legend,
.darkMode .formulario label {
  color: #e1e1e1;
}
.darkMode .formulario input:not([type=submit]),
.darkMode .formulario textarea,
.darkMode .formulario select {
  background-color: #333333;
  color: #e1e1e1;
  border: solid #333333;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 767px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.5s linear;
  height: 0;
}
@media (min-width: 767px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}
@media (min-width: 767px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
    font-size: 1.8rem;
  }
}
.navegacion a:hover {
  color: #71B100;
}

@media (min-width: 767px) {
  .barra-menu {
    display: none;
  }
}
.barra-menu img {
  width: 4rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.no_webp .inicio {
  background-image: url(../img/header.png);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

@media (min-width: 767px) {
  .barra {
    align-items: flex-end;
    margin: 2rem;
  }
}

.dark-mode {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 767px) {
  .dark-mode {
    align-items: flex-end;
  }
}

.dark-mode-boton {
  width: 3rem;
  margin: 2rem;
  filter: invert(100%);
}

.header {
  background-color: #333333;
  padding: 3rem;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 767px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-size: 700;
  max-width: 68rem;
}

.footer {
  background-color: #333333;
  padding: 5rem;
  margin: 0;
}
.footer .navegacion {
  display: flex;
  visibility: visible;
  opacity: 1;
  gap: 1rem;
  margin-bottom: 5rem;
}

.copyright {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 767px) {
  .contenedor-iconos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.contenedor-iconos img {
  display: inline-block;
  width: 10rem;
  height: 10rem;
}

.icono {
  text-align: center;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 1024px) {
  .contenedor-anuncios {
    margin: 0 2rem;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.contenedor-info {
  padding: 3rem 2rem;
  background-color: #e1e1e1;
}
.contenedor-info .precio {
  color: #71B100;
  font-weight: 900;
}
.contenedor-info h3 {
  margin: 0;
}
.contenedor-info ul {
  display: flex;
  list-style: none;
  margin-bottom: 1rem;
  padding: 0;
}
.contenedor-info ul li {
  display: flex;
  align-items: center;
  margin: 0 3rem;
  width: 5rem;
}

.no_webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  padding: 5rem;
  align-items: center;
}
.imagen-contacto h2 {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: 900;
}
.imagen-contacto p {
  color: #FFFFFF;
}

@media (min-width: 767px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
  .blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.seccion-inferior h3 {
  font-weight: 900;
}

.blog {
  margin: 3rem;
}
.blog h4 {
  margin-top: 0;
  padding-top: 0;
}
.blog h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
}

.texto-entrada span {
  font-weight: 900;
  color: #71B100;
}

.testimonial {
  background-color: #71B100;
  color: #FFFFFF;
  font-size: 2rem;
  border-radius: 2rem;
  padding: 3rem 2rem 2rem 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  display: block;
  width: 6rem;
  height: 8rem;
  position: absolute;
  left: 1rem;
}
.testimonial p {
  margin: 3rem;
  text-align: end;
  color: #FFFFFF;
  font-weight: 700;
}

.nosotros {
  margin: 3rem;
}
@media (min-width: 767px) {
  .nosotros {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 3rem;
  }
}
.nosotros h2 {
  margin-top: 0;
  font-weight: 900;
}

.contenedor-nosotros::after {
  content: "";
  display: block;
  background-color: #71B100;
  height: 0.5rem;
  width: auto;
  margin: 0 10rem;
  align-content: center;
}

.formulario fieldset {
  display: flex;
  flex-direction: column;
}
.formulario legend {
  color: #333333;
}
.formulario label {
  text-transform: uppercase;
  padding-left: 2rem;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  border: 1px solid;
  border-radius: 1rem;
}
.formulario textarea {
  resize: none;
  height: 15rem;
}
.formulario p {
  font-size: 1.4rem;
  padding-top: 1rem;
}